<template>
  <div>
    <!-- 1: -->
    <!-- <mt-header fixed title=" i ">
      <mt-button icon="back" slot="left"></mt-button>
      <mt-button icon="more" @click="openNav()" slot="right">
        <div id="side">
          <div id="mySidenav" class="sidenav">
          <span class="closebtn" @click.stop="closeNav()">
            &times;
          </span>
            <router-link to="login"></router-link>
            <router-link to="register"></router-link>
          </div>
        </div>
      </mt-button>
    </mt-header> -->
    <!-- -->
    <router-view/>
   <mt-tabbar v-model="message" fixed>
  <mt-tab-item id="Home">
  <img slot="icon" src="../img/tabs/index.png">
          
  </mt-tab-item>
  <mt-tab-item id="GoodList">
  <img slot="icon" src="../img/tabs/product.png">
          
  </mt-tab-item>
  <mt-tab-item id="ShopCart">
          <img slot="icon" src="../img/tabs/shop.png">
          
  </mt-tab-item>
  <mt-tab-item id="Me">
   <img slot="icon" src="../img/me.png">
            
  </mt-tab-item>
   </mt-tabbar>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        //tabbarmessageselected
        message:this.selected,
        //icon
        atabs:this.tabs,
      }
    },
    props:{
      selected: String,
      tabs:Array,
    },
    watch: {
      message: function (val, oldVal) {
        //  val 
        switch(val){
          case 'Home':
            this.$router.push('/Home');
            break;
          case 'GoodList':
            this.$router.push('/GoodList');
            break;
          case 'ShopCart':
            this.$router.push('/ShopCart');
            break;
          case 'Me':
            this.$router.push('/Me');
            break;
        }
      }
    },
  }

</script>
<style>
.mint-tab-item-icon > *{
  margin-left:-7px;
}
</style>



