<template>
  <div class="app-shopcart">
    <!--  -->
    <mt-header fixed title="">
      <router-link to="/GoodInfo" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <mt-button icon="more" slot="right"></mt-button>
    </mt-header>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <div class="mui-card-content-inner">
            <div class="dingdan"> :</div>
              <p>
                <span> : {{this.addrlist.name}}  </span>
              </p>
              <p> : {{addrlist.tle}}</p>
              <p>: {{addrlist.place}}</p>
            </div>
        </div>
      </div>
    </div>
    <!--  -->
    <!--  -->
    <!--  -->
    <ul class="mui-table-view">
      <li class="mui-table-view-cell mui-media" 
      v-for="(item,i) of shoplist" :key=i>
        <!--  -->
        <input type="checkbox" class="check" :checked="item.cb" :data-id="item.id" :data-i="i" @click="modifyItem">
          <img class="mui-media-object mui-pull-left" :src="item.pic">
          <div class="mui-media-body">
            {{item.pname}}
            <p class='mui-ellipsis'>
              {{item.price}}
              <input type="button" class="del" value="" @click="removeItem" :data-id="item.id" :data-idx="i"/>
              </p>    
          </div> 
      </li>
    </ul>
    <!--  -->
    <div class="footer">
      <div class="quan">
        <!--  -->
        <input type="checkbox" class="qx" @click="selectAll" :checked="allcb">
        <p class="xx">
          <span></span>
          <!--  -->
          <input type="button" class="btn" value="" @click="delItem">
        </p>
      </div>
      <div class="zong">
        <p>
          <span>:</span>
          <span>{{total.toFixed(2)}}</span>
        </p>
        <p>
          <span> {{total.toFixed(2)}}0.00</span>  
        </p>
      </div>
      <div class="jie" @click="mes"></div>
    </div>  
  </div>
</template>
<script>
import {Toast} from "mint-ui";
//
import {MessageBox} from 'mint-ui'
export default {
  data(){
    return{
      addrlist:[], //
      // id:1,  //
      shoplist:[],
      uid:1,
      allcb:false
    }
  },
  created(){
    this.addr();
    this.loadMore();
  },
  //
  computed:{
    total(){
      var sum=0;
      for(var p of this.shoplist){
        if(this.allcb){
        sum+=p.price;
      }
    }
    return sum;
    }
  },
  methods:{
    //
    mes(){
      if(this.allcb){
        MessageBox('', '');
        this.shoplist='';
        this.$router.push("/Merorder");
      }else{
        MessageBox('', '');
      }
    },
    //
    selectAll(e){
      //1.
      var rs=e.target.checked;
      //2.
      for(var item of this.shoplist){
        item.cb= rs;
      }
      //3.item.cb=
      //4.
      this.allcb=rs;  
    },
    //  
    modifyItem(e){
      //1. true false
      var rs=e.target.checked;
      //2.
      var i=e.target.dataset.i;
      //3.cb
      this.shoplist[i].cb=rs;
    },
    //
    delItem(){
      //[{cb:true,id:1},{cb:true,id:3}] "1,3"
      var html="";
      for(var item of this.shoplist){
        if(item.cb){
          html+=item.id+",";
        }
      }
      if(html.length==0){
        Toast("!");
        return;
      }
      //, 1,6,7
      html=html.substring(0,html.length-1);
      console.log(html);
      //ajax ids=1,6,7
      var url="removeMItem?ids="+html;
      this.axios.get(url).then(result=>{
        if(result.data.code==1){
          Toast("");
          this.loadMore();
        }else{
          Toast("");
        }
      })
    },
    //
    removeItem(e){
      //1.id
      var id=e.target.dataset.id;
      var idx=e.target.dataset.idx;
      //2.url
      var url="http://127.0.0.1:3000";
      url+="/removeItem";
      var postData=this.qs.stringify({
        id:id
      });
      //3.ajax
      this.axios.post(url,postData).then(result=>{
        console.log(result);
        if(result.data.code==1){
          //
          //1:
          //2:
          //
          //splice(,)
          this.shoplist.splice(idx,1);
          Toast("");
        }else{
          Toast("");
        }
      })
    },
    //
    loadMore(){
      var url="cart";
      this.axios.get(url).then(result=>{
        //console.log(result.data)
        this.shoplist=result.data.data;
        //:
        var rows=result.data.data;
        //cbfalse
        for(var item of rows){
          item.cb=false;
        }
        //shoplist
        this.shoplist=rows;
        console.log(this.shoplist);
      })
    },
    addr(){
      var url="addr";
      this.axios.get(url).then(result=>{
        //console.log(result.data);
        this.addrlist=result.data.data[0];
      })
    }
  }
  
}
</script>
<style>
/*  */
.mui-card{
  box-shadow: 0px 1px 10px 1px #d2d1d3; 
  margin-top:15px !important;
  
  top:35px;
  height: 112px;
  width:345px;
  margin:15px;
}
p{
  margin-left: 20px;
}
/**/
.mui-table-view{
  margin-top:50px !important;
}
.mui-table-view .mui-media-object{
  max-width:75px  !important;
  height:75px !important;
  position: relative;
  left: 17px;
  top: 5px;
}
.mui-table-view .mui-media-body{
  font-size:13px;
  position: relative;
  left: 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.mui-table-view-cell p{
  line-height:16px;
  color:#e93b3d;
  font-size:16px;
  margin-left: 5px;
  margin-top: 5px;
}
input[type='checkbox']{
  position: relative;
  left: -86px;
  top: 28px;
}
.del{
  position: relative;
  left:30px;
  height: 30px;
  width:70px;
  padding:0 !important;
  height:20px;
  width:50px;
  margin-left:110px;
}
/**/
.footer{
  display:flex;
  height:60px;
  background-color:#fff;
  margin-top:10px;
  position: fixed;
  bottom:0;
}
.footer .quan{
  width:100px;
  margin-left:3%;
  margin-right:15px;
}
.footer .quan .p{
  margin-left:5px;
}
.footer .quan .xx span{
  position: relative;
  top:16px;
  left:20px;
}
.footer .quan .qx{
  position: relative;
  left:5%;
  top:35px;
}
.footer .quan .btn{
  height:25px;
  width:90px;
  position:relative;
  top:-40px !important;
  line-height: 25px;
  padding:0;
  margin-top:12px !important;
} 
.footer .zong{
  margin-top: 5px;
  width:130px;
  /* margin-left: 35px; */
  line-height: 15px;
}
.footer .zong p{
  margin:0;
  color:#000;
  font-size:12px;
}
.footer .zong p:first-child{
  margin-top:8px;
  margin-bottom:5px;
}
.footer .zong p:first-child span:first-child{
  font-weight:700;
  font-size:16px;
  margin-right:5px;
}
.footer .zong p:first-child span:last-child{
  color:red;
  font-size:15px;
}
.footer .zong p:last-child{
  color:#666;
}
.footer .zong p:last-child span{
  position: relative;
  top:0px;
}
.footer .jie{
  background-color:#e4393c;
  width:100px;
  font-size:18px;
  color:#fff;
  line-height: 60px;
  text-align:center;
  border-radius:2px;
  margin-left:30px;;
}
/**/
.mint-header.is-fixed{
  background-color:#fff;
  color: #000;
  font-size: 18px
}
.content .price span:last-child{
  margin-left:68%;
}
/**/
.dingdan {
  margin-bottom: 5px;
  margin-top: -7px;
}
p{
 color:#666 !important;
  margin-left:2%;
  line-height:14px;
} 
.mui-card-content-inner{
  padding:8px !important;
}
.min-card{
  margin:15px;
}
.mui-card{
  left:4px;
}
</style>



