<template>  
  <div class="app-login">
    <div id="title">
    <img src="../img/icon.png">
    <h1></h1>
    </div>
    <input type="text" name="uname" v-model="uname" placeholder="" class="inp"><br>
    <input type="password" name="upwd" v-model="upwd" placeholder="" class="inp"><br>
    <mt-button @click="btnClick" size="large" type="primary"></mt-button>
  </div>
</template>
<script>
//1.mint-ui Toast
import {Toast} from "mint-ui"
export default {
  data(){
    return {
      uname:"",
      upwd:""
    }
  },
  methods:{
    btnClick(){
      //1.
      var uname=this.uname;
      var upwd=this.upwd;
      var reg=/^[a-z0-9_]{1,8}$/i;
      if(!reg.test(uname)){
        Toast(",")
        return;
      }
      if(!reg.test(upwd)){
        Toast(",")
        return;
      }
      var postData=this.qs.stringify({
        uname:uname,
        upwd:upwd
      })
      //console.log(postData);
      //3.ajax
      var url="login";
      this.axios.post(url,postData).then(result=>{
        if(result.data.code==1){
          Toast("");
          this.$router.push("/Home");
        } 
      });
    }
  }
}
</script>
<style scoped>
#title{
    text-align: center;
    margin-bottom: 30px;
}
.inp{
    border:none;
    background: #efeff4;
    border-bottom: 1px solid #aaa9a9;
}
</style>



