import Vue from 'vue'
import App from './App.vue'
import router from './router'


Vue.config.productionTip=false
//0.1:Vuex
import Vuex from "vuex"
//0.2:Vuex
Vue.use(Vuex)
//0.3:store
var store = new Vuex.Store({
  //
  state:{
    cartCount:sessionStorage.getItem("cartCount")||0
  },
  //
  mutations:{
    increment(state){
      state.cartCount++;
      sessionStorage.setItem("cartCount",state.cartCount);
    }
  },
  //
  getters:{
    optCartCount:function(state){
      return state.cartCount;
    }
  }
})
//0.4:storeVue

Vue.config.productionTip = false

import'mint-ui/lib/style.css'
import './lib/mui/css/mui.css'
//  MUI 
// 
import './lib/mui/css/icons-extra.css'
//1.Header
import {Header,Button,Swipe,SwipeItem,Tabbar,TabItem,Search,Cell,Navbar,TabContainer,TabContainerItem} from "mint-ui"

//2.Header
Vue.component(TabContainer.name, TabContainer);
Vue.component(TabContainerItem.name, TabContainerItem);
Vue.component(Navbar.name, Navbar);
Vue.component(Cell.name, Cell);
Vue.component(Search.name,Search);
Vue.component(Header.name,Header);
Vue.component(Button.name,Button);
Vue.component(Swipe.name,Swipe);
Vue.component(SwipeItem.name,SwipeItem);
Vue.component(Tabbar.name,Tabbar);
Vue.component(TabItem.name,TabItem);
//3.axios
import axios from "axios"
//4.session
axios.defaults.withCredentials=true;
//5.axiosVue
Vue.prototype.axios=axios
//6.
Vue.filter("datatimeFilter",function(val){
  //6.1:
  var now=new Date(val);
  //6.2:   
  var y=now.getFullYear();
  var m=now.getMonth()+1;
  var d=now.getDate();
  //6.3:   
  var h=now.getHours();
  var mi=now.getMinutes();
  var s=now.getSeconds();
  //6.4: 100
  m<10&&(m="0"+m);
  d<10&&(d="0"+d);
  //6.5:
  return `${y}-${m}-${d} ${h}:${mi}:${s}`;
});
//7. main.js  qs
//axios post 
import qs from "qs";
Vue.prototype.qs=qs;



new Vue({
  router,
  render: h => h(App),
  store       //0.4  storeVue
}).$mount('#app')



