	},
	methods:{
		// 
		getShopcart(){
			var pid=this.lid;
			var price=this.info[0].price;
			console.log(this.info)
			var pname=this.info[0].title;
			var pic=this.info[0].pic;
			var url="getShopcart?pid="+pid+"&price="+price+"&pname="+pname+"&pic="+pic;
			//
			this.axios(url).then(result=>{
				console.log(result);
				this.$store.commit("increment");
			});
		},
		handlePic(){
			//console.log("123");
      var url = "pic?lid="+this.lid;
      this.axios.get(url).then(result => {
				this.pic = result.data.data;
				//console.log(this.pic)
      });
		},
		handleDetails(){
      //console.log("123");
      var url = "details?lid="+this.lid;
      this.axios.get(url).then(result => {
				this.info = result.data.data;
				// console.log(this.info)
      });
    },
