<template>
  <div class="app-goodlist">
    <!--  -->
    <div class="search">
      <input type="text" placeholder="" v-model="sousuo">
      <mt-button class="btn" @click="cha"></mt-button>
    </div>
    <!--  start -->
    <div class="goods">
      <div class="goods-item" v-for="item of list">
        <!-- GoodList.vue -->
        <img :src="item.img" @click.prevent="jumpInfo" :data-lid=item.id>
        <div class="title">
          <span>{{item.title}}</span>
          </div>
        <div class="info">
          <span class="now">{{item.price}}</span>
        </div>
        <div class="sell">
          <span>{{item.comment}}</span>
          <span>{{item.good}}</span>
        </div>
        <!--  end -->
      </div>
    </div> 
    <!--  -->
    <mt-button class="aa" size="large" type="primary" @click="loadMore"></mt-button>
    <!--  -->
    <mt-tabbar fixed>
      <router-link to='/'>
        <mt-tab-item id="tab1" class="shouye"  >
          <img slot="icon" src="../../img/tabs/index.png">
          
        </mt-tab-item>
       </router-link>
        <!-- <router-link to='/'> -->
        <mt-tab-item id="tab2" class="haha">
          <img slot="icon" src="../../img/tabs/product-active.png">
          
        </mt-tab-item>
         <!-- </router-link> -->
      <router-link to="/ShopCart">
        <mt-tab-item id="tab3"  class="shop">
          <img slot="icon" src="../../img/tabs/shop.png">
          
        </mt-tab-item>
      </router-link>
      <router-link to="/Me">
        <mt-tab-item id="tab4"  class="me">
          <img slot="icon" src="../../img/me.png">
          
        </mt-tab-item>
      </router-link>
    </mt-tabbar>
  </div>
</template>
<script>
import {Toast} from "mint-ui";
export default {
  data(){ 
    return{
      list:[],  //
      pno:0,    //   0
      pageSize:6,
      btn:"",  //
      sousuo:""
    }
  },
  created(){
    //
    this.loadMore();
  },
  methods:{
    //
    jumpInfo(e){
      var lid=e.target.dataset.lid;
      //console.log(lid);
      this.$router.push("/GoodInfo?lid="+lid);
    },
    //
    loadMore(){
      this.pno++;
      // console.log(this.pno)
      //1.url
      var url= "http://127.0.0.1:3000";
      url+="/getProducts?pno="+this.pno+"&pageSize="+this.pageSize;
      //console.log(this.pageSize);
      //2.ajax
      this.axios.get(url).then(result=>{
        //3.data
        //3. list
        //concat 
        // var rows = result.data;
        for(var item of result.data){
          this.list.push( item);
        }
        this.pageSize=4;
      })
    },
    cha(){
      this.list=[];
      var url="search?sousuo="   +this.sousuo;
      this.axios.get(url).then(result=>{
         console.log(result.data.data)
        var arr=result.data.data;
        if(arr.length==0){
          Toast(",")
        }else{
          this.list=arr;
          // console.log(this.list);
        } 
      })
    }
  },
}
</script>
<style>
/**/
.app-goodlist .search{
  width: 100%;
  left:0;
  background-color:#99c2c7;
  position:fixed;
}
.app-goodlist .search input{
  width:70%;
  height:30px;
  margin-top:5px;
  margin-left:20px;
  margin-right:10px;
  margin-bottom:0;
  border-radius:100px;
}
.app-goodlist .search .btn{
  width:60px;
  height:30px;
  margin-top:5px;
  margin-bottom:5px;
}

/**/
.app-goodlist .goods{
  padding-top:40px !important;
  display:flex; /**/
  flex-wrap: wrap; /**/
  justify-content: space-between; /**/
  padding:4px;
}
/**/
.app-goodlist .goods-item{
  width:49%; /**/
  border:1px solid #ccc; /**/
  margin:2px 0;  /*  */
  padding:2px;
  display:flex;/*  */
  flex-direction:column; /* : */
  min-height:245px; /**/
}
/**/
.app-goodlist .goods-item img{
  width:100%;
}
/**/
.title>span{
  height:36px;
  font-size: 13px;
  color:#333;
  letter-spacing: 0;
  line-height: 18px;
  word-break: break-all;
}
.title{
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.now{
  color:#e93b3d;
  font-size:13px;
}
.sell{
  font-size:12px;
  color:#999;
  line-height: 18px;
  min-height: 18px;
}
.sell :last-child{
  margin-left:15px;
}
.aa{
  margin-bottom:58px;
}
.mint-button--primary{
  background-color:#99c2c7 !important;
}
/**/
.shouye{
  position: relative;
  left:40px;
}
.haha{
  position: relative;
  left:-20px !important;
}
.shop{
  position: relative;
  left:-75px;
}
.me{
  position: relative;
  left:-30px;
}
</style>





