<template>
<div class="container">
  <mt-header fixed title="">
    <router-link to="/Me" slot="left">
      <mt-button icon="back"></mt-button>
    </router-link>
  </mt-header>
  <mt-navbar v-model="selected">
    <mt-tab-item id="fu"></mt-tab-item>
    <mt-tab-item id="fa"></mt-tab-item>
    <mt-tab-item id="shou"></mt-tab-item>
    <mt-tab-item id="tui">/</mt-tab-item>
  </mt-navbar>
  <mt-tab-container v-model="selected">
    <mt-tab-container-item id="fu">
      <div class="duo">
        <img class="ding" src="../../img/ding.png" >
      </div>
      <p class="nin"></p>
    </mt-tab-container-item>
    <!--  -->
    <mt-tab-container-item id="fa">
      <p class="mai">,</p>
      <ul class="mui-table-view">
        <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
          <img class="mui-media-object mui-pull-left" :src="item.pic">
          <div class="mui-media-body">
            {{item.pname}}
            <p class='mui-ellipsis'>{{item.price}}</p>
            <button class="bta" @click="remind"></button>
          </div>
        </li>
      </ul>
    </mt-tab-container-item>
    <!--  -->
    <mt-tab-container-item id="shou">
      <ul class="mui-table-view">
        <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
          <img class="mui-media-object mui-pull-left" :src="item.pic">
          <div class="mui-media-body">
            {{item.pname}}
            <p class='mui-ellipsis'>{{item.price}}</p>
            <button class="bta" @click="remind"></button>
          </div>
        </li>
      </ul>
    </mt-tab-container-item>
    <!-- / -->
    <mt-tab-container-item id="tui">
      <ul class="mui-table-view">
        <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
          <img class="mui-media-object mui-pull-left" :src="item.pic">
          <div class="mui-media-body">
            {{item.pname}}
            <p class='mui-ellipsis'>{{item.price}}</p>
            <button class="btt" @click="refund"></button>
          </div>
        </li>
      </ul>
    </mt-tab-container-item>
  </mt-tab-container>
</div>
</template>
<script>
export default {
  data () {
    return{
      selected:"fu", //
      uid:1,
      shoplist:[]
    }
  },
  created(){
    this.loadMore();
  },
  methods:{
    //
    loadMore(){
      var url="cart?uid="+this.uid;
      this.axios.get(url).then(result=>{
        //console.log(result.data.data);
        this.shoplist=result.data.data;
        console.log(this.shoplist);
      })
    },
    //
    remind(){
          Toast("...");
    },
    //
    refund(){
        Toast(",24")
    }
  }  
}
</script>
<style>
.container{
  padding-top:40px !important;
}
.mint-tab-item-label{
  font-size:14px !important;
}
.ding{
  width:100%;
  height:100%;
}
.duo{
  width:260px;
  height:425px;
  position: relative;
  left:55px;
}
.nin{
  margin-right: 5px;
  margin-left:120px;
}
.mai{
  margin-top:10px;
}
.bta{
    position: relative;
    top:-18px;
    left:180px;
    padding:2px;
}
.btt{
  position: relative;
    top:-18px;
    width:55px;
    left:190px !important;
    padding:2px;
} 
</style>



